<template>
  <div class="profile">
    <div class="profile-card">
      <portal-header :projectObj="titleConfig" :showBottomLine="false"></portal-header>
      <div class="profile-divider-line"></div>
      <div class="panel-block">
        <basic-info-group :basicInfo="basicInfo" :groups="groups"></basic-info-group>
      </div>
    </div>

    <simple-collapse class="collapse-block" title="Limit Rule">
      <el-divider style="margin: 0 0 10px" />
      <div class="limit-table">
        <table-page
          size="small"
          :isSingleCheck="false"
          :has-pagination="false"
          :columns="columns"
          :outSideTableData="tableData"
        >
        </table-page>
      </div>
    </simple-collapse>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import TablePage from '@/components/tablePage';
import BasicInfoGroup from '@/views/identifier-center/pages/organization/components/basic-info-group';
import SimpleCollapse from '@/views/identifier-center/pages/organization/components/simple-collapse';
import { queryRuleProfileDetail } from '@/views/identifier-center/api/api-organization';
import dayjs from 'dayjs';

export default {
  name: 'rule-profile-detail',
  components: {
    PortalHeader,
    BasicInfoGroup,
    TablePage,
    SimpleCollapse,
  },

  data() {
    return {
      titleConfig: {
        title: this.$t('IDC.ruleProfile.titleConfig.title'),
        desc: this.$t('IDC.ruleProfile.titleConfig.desc'),
      },
      basicInfo: {
        id: '',
        ruleProfileType: '',
        identityType: '',
        name: '',
        description: '',
        expireTime: '',
      },
      groups: [
        {
          label: this.$t('IDC.ruleProfile.basicInfo'),
          list: [
            {
              label: this.$t('IDC.ruleProfile.ruleProfileID'),
              name: 'id',
            },
            {
              label: this.$t('IDC.ruleProfile.ruleProfileType'),
              name: 'ruleProfileType',
            },
            {
              label: this.$t('IDC.ruleProfile.ruleProfileName'),
              name: 'ruleProfileName',
            },
            {
              label: this.$t('common.label.description'),
              name: 'description',
            },
            {
              label: this.$t('IDC.ruleProfile.validityPeriod'),
              name: 'validityPeriod',
            },
          ],
        },
      ],

      columns: [
        { label: this.$t('IDC.ruleProfile.limitRuleName'), prop: 'limitRuleName', align: 'left' },
        { label: this.$t('common.label.status'), prop: 'status', align: 'left' },
        { label: this.$t('IDC.ruleProfile.currency'), prop: 'currency', align: 'left' },
        { label: this.$t('IDC.ruleProfile.value'), prop: 'thresholdValueStr', align: 'left' },
      ],
      tableData: [],
    };
  },

  computed: {},

  created() {
    this.init();
  },
  methods: {
    async init() {
      const result = await this.getDetail();
      if (!result) {
        result;
      }
      if (result.profile) {
        this.setBasicInfo(result.profile);
      }
      if (result.limitInstances) {
        this.setTable(result.limitInstances);
      }
    },

    setBasicInfo(config) {
      const applyTime = config.applyTime ? dayjs(config.applyTime).format('DD/MM/YYYY HH:mm:ss') : '-';
      const expireTime = config.expireTime ? dayjs(config.expireTime).format('DD/MM/YYYY HH:mm:ss') : '-';
      config.validityPeriod = applyTime + ' ~ ' + expireTime;
      this.basicInfo = config;
    },
    setTable(list) {
      this.tableData = list;
    },

    async getDetail() {
      const params = this.getRouteParam();
      if (!params) {
        return;
      }
      const id = params.id;
      if (!id) {
        return;
      }

      try {
        const res = await queryRuleProfileDetail(id);
        if (res.header && res.header.success) {
          return res;
        }
        return null;
      } catch (e) {
        return null;
      }
    },
    getRouteParam() {
      let params = this.$route.query.data;
      if (!params) {
        return null;
      }
      params = decodeURIComponent(params);
      if (!params) {
        return null;
      }
      return JSON.parse(params);
    },
  },
};
</script>

