<template>
  <div class="simple-collapse">
    <div class="simple-collapse__header" :class="disabled ? 'is-disabled' : ''" @click="toggle">
      <i class="el-icon-arrow-right" :class="actived ? 'opened' : ''" v-if="showArrowIcon"></i>
      <span class="simple-collapse__header-value">{{ title }}</span>
    </div>
    <el-collapse-transition>
      <div class="el-collapse-item__wrap simple-collapse__content" v-show="actived">
        <slot></slot>
      </div>
    </el-collapse-transition>
  </div>
</template>

<script>
export default {
  name: 'CodesOrgCollapse',

  props: {
    title: {
      type: String,
      default: '',
    },
    opened: {
      type: Boolean,
      default: true,
    },
    showArrowIcon: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    return {
      actived: this.opened,
    };
  },

  mounted() {},

  methods: {
    toggle() {
      if (this.disabled) {
        return;
      }
      this.actived = !this.actived;
    },
  },
};
</script>

<style lang="scss" scoped>
.simple-collapse {
  background: $--text_color--white;

  &__header {
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    text-align: left;
    padding-top: 20px;
    padding-bottom: 13px;
    cursor: pointer;

    .el-icon-arrow-right {
      margin-right: 4px;
      transition: all 0.3s linear;

      &:before {
        content: '\e791';
      }
    }
    .el-icon-arrow-right.opened {
      transform: rotate(90deg);
    }
  }
  &__header.is-disabled {
    cursor: unset;
  }
}
</style>
